.header {
    padding: 28px 0 39px;
    
    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo {
        max-width: 172px;
        width: 100%;
        img {
            max-height: 60px;
        }
    }

    &__navigation {
        width: 100%;
        &-list {
            display: flex;
            align-items: center;
            justify-content: space-between;
            max-width: 602px;
            width: 100%;
            margin: 4px auto 0;
            padding: 0;
        } 
        &-item-link {
            color: #fff;
            text-decoration: none;
            &:hover {
                color:$color-primary;
            }
        }
    }
}
.header__navigation {
    display: none;
    &.show {
        display: flex;
    }
}

.header .btn {
padding: 13px;
width: 171px;
}

.btn-burger {
    padding: 1.3px 13.5px;
    border: none;
    position: relative;
    
    &::before,
    &::after {
        content: "";
        top: -11px;
        left: 0;
        position: absolute;
        height: 2.5px;
        width: 27px;
        background: #fff;
        transition: .3s;
    }
    
    &::after {
    top: initial;
    bottom: -11px;
    left: 0;
    }
    
    &.open {
        background: transparent;
        &::before{
            transform: rotate(-45deg) translate(-6px, 6px);
        }
        &:after {
            transform: rotate(45deg) translate(-10px, -10px);
        }
    }
}